\* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  text-align: center;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

form {
  background-color: #d7e0f5;
  padding-top: 20px;
  padding-bottom: 20px;
}

input, button {
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 20px;
}

input {
  border: 1px solid #afb1b8;
  outline: none;
  transition: color .25s linear;
}

input:hover, input:focus {
  border: 1px solid #188ce8;
}

form button {
  color: #fff;
  background-color: #188ce8;
  border-color: #fff;
}

form button:hover, form button:focus {
  color: #188ce8;
  background-color: #fff;
  border-color: #fff;
}

button {
  color: #188ce8;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #188ce8;
  transition: color .25s linear;
}

button:hover, button:focus {
  color: #fff;
  background-color: #188ce8;
  border-color: #188ce8;
}

.load-more {
  margin-top: 20px;
}

.load-more:disabled {
  color: #fff;
  cursor: not-allowed;
  background-color: #c1c1c1;
  border-color: #fff;
}

.load-more.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.photo-card {
  width: 30%;
  text-align: center;
  background-color: #fff;
  border: 1px solid #188ce8;
  border-radius: 5px;
  padding: 20px;
}

img {
  max-width: 100%;
  border-radius: 5px;
  margin-bottom: 15px;
  display: block;
}

.info {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.info-item {
  color: #188ce8;
  flex-flow: column wrap;
  margin: 0;
  display: flex;
}

